<template>
  <div>
    <section class="head">
      <div><a @click="goBack" class="iconfont back"> &#xe614; </a></div>
      <h1>Orders</h1>
      <div class="chartWrap">
        <a class="iconfont cart" @click="$router.push('cart')"> &#xe669; </a>
      </div>
    </section>
    <div class="orderDetail">
      <ul>
        <li>
          <van-cell-group>
            <van-cell
              title="Basic Inforation"
              :value="orderDetail.orderStatus"
            />
          </van-cell-group>
          <div class="basicInforation">
            <div class="coat">
              <span class="title">Order No:</span
              ><span class="value">{{ orderDetail.orderNo }}</span>
            </div>
            <div class="coat">
              <span class="title">Date:</span
              ><span class="value">{{ orderDetail.orderDate }}</span>
            </div>
            <div class="coat">
              <span class="title">Payment:</span
              ><span class="value">{{ orderDetail.payment }}</span>
            </div>
            <div class="coat">
              <span class="title">Grand Total:</span
              ><span class="value"
                >{{ orderDetail.grandTotal }} AED ({{
                  orderDetail.prodList.length
                }}
                Commodities)</span
              >
            </div>
            <div class="coat" @click="$router.push({path:'/logistics',query:{orderNo:orderDetail.orderNo}})">
              <span class="title">Recent Logistic:</span
              ><span class="value"
                >{{ orderDetail.recentLogistic }}
                <van-icon color="#62ade7" name="arrow"
              /></span>
            </div>
            <div class="coat">
              <span class="title">Shipment:</span
              ><span class="value">{{ orderDetail.shipment }} </span>
            </div>
          </div>
        </li>
        <li>
          <van-cell-group>
            <van-cell title="Commmodities" />
          </van-cell-group>
          <div
            class="commmoWrap"
            v-for="(item, index) in orderDetail.prodList"
            :key="index"
          >
            <p>{{ orderDetail.dliverydDays }}</p>
            <div class="commmo" @click="
            $router.push({
                    path: '/proDetail',
                    query: { spuCode: item.spuCode}
                  })
               ">
              <div class="photoWrap">
                <img :src="item.img0" />
              </div>
              <div class="information">
                <div class="coat">
                  <span class="title">{{ item.productName }}</span
                  ><span class="value">x1</span>
                </div>
                <div class="model">
                  <span class="title">{{
                    getAttributes(item.attributeList)
                  }}</span
                  ><span class="value">{{ item.refundStatus }}</span>
                </div>
                <div class="retreat">
                  <div class="title"><p class="present">{{ item.productAmount }} AED</p>
                    <p class="origin">{{ item.tagPrice }} AED</p>
                  </div>
                  <div class="value">
                  <span  v-if="item.canRefund!='0'" @click="refund(item)" >Refund</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </li>
        <li>
          <van-cell-group>
            <van-cell title="Address Information" />
          </van-cell-group>
          <div class="siteWrap">
            <div class="addressData">
              <span class="surname">{{
                orderDetail.userAddress.userName
              }}</span>
              <span class="mobile"
                >NO.+971 {{ orderDetail.userAddress.mobile }}</span
              >
              <span v-if="orderDetail.userAddress.landmark" class="pag">{{ orderDetail.userAddress.landmark }}</span>
            </div>
            <div class="address">
              Address {{ orderDetail.userAddress.address }}
            </div>
          </div>
        </li>
        <li>
          <van-cell-group>
            <van-cell title="Payment Summary" />
          </van-cell-group>
          <div class="payment">
            <div class="coat">
              <span class="title">Sub Total:</span
              ><span class="value">{{ orderDetail.subTotal }}AED</span>
            </div>
            <div class="coat">
              <span class="title">Shipping:</span
              ><span class="value">{{ orderDetail.shipping }}AED</span>
            </div>
            <div class="coat" v-if="false">
              <span class="title">Import Fees Deposit:</span
              ><span class="value">78.00AED</span>
            </div>
            <div class="coat">
              <span class="title">Coupon:</span
              ><span class="value">-{{ orderDetail.couponAmount }}AED</span>
            </div>
            <div class="coat">
              <span class="title">Grand Total:</span
              ><span class="value" style="color: #E33946;font-size: 14px;"
                >{{ orderDetail.grandTotal }}AED</span
              >
            </div>
          </div>
        </li>
        <li>
          <div class="btmWrap">
            <div class="contact" @click="toTel">
              Contact Us: <font>400-525-5222</font>
            </div>
            <div class="btn" v-if="orderDetail.orderStatus == 'Canceled'">
              <span class="review" @click="review">Review</span>
              <span class="buyAgain" @click="buyAgain">Buy Again</span>
            </div>
            <div class="btn" v-if="orderDetail.orderStatus == 'Done'">
              <span class="review" @click="review">Review</span>
              <span class="review" @click="myReview">My Reviews</span>
              <span class="buyAgain" @click="buyAgain">Buy Again</span>
            </div>
            <div class="btn" v-if="orderDetail.orderStatus == 'Transporting'">
              <span class="review" @click="received">Received</span>
              <span class="buyAgain" @click="$router.push({path:'/logistics',query:{orderNo:orderDetail.orderNo}})">Logistics</span>
            </div>
            <div class="btn" v-if="orderDetail.orderStatus == 'Unpaid'">
              <span class="review" @click="cancel">Cancel</span>
              <span class="buyAgain" @click="$router.push({path:'payMent',query:{orderNo:orderDetail.orderNo}})">Pay</span>
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { orderDetail,goRefund,cancelOrder,confirmOrder} from "@/api/order";
export default {
  name: "orderDetail",
  data() {
    return {
      orderDetail: { userAddress: {}, prodList: [] },
      orderNo: this.$route.query.orderNo
    };
  },
  computed: {},
  mounted() {
    this.init();
  },
  methods: {
    init() {
      orderDetail({ orderNo: this.orderNo }).then(res => {
        this.orderDetail = res.data.result;
      });
    },
    changeSort(val) {
      this.sortValue = val;
      this.sortVisible = false;
    },
    goBack() {
      this.$router.back(-1);
    },
    getAttributes(arr) {
      return arr.map(t => t.optionName).join(";");
    },
    toTel() {
       this.$dialog
        .confirm({
          message: "Are you sure to dial the phone??",
          confirmButtonText: "Confirm",
          cancelButtonText: "Cancel"
        })
        .then(() => {
          // on confirm
          window.location.href = 'tel://4005255222'

        })
        .catch(() => {
          // on cancel
        });
    },
    review(){
        this.$router.push({path:"reviewsOrder",query:{orderNo:this.orderNo}})
    },
    cancel(){
      this.$dialog
        .confirm({
          message: "Are you sure to cancel this order?",
          confirmButtonText: "Confirm",
          cancelButtonText: "Cancel"
        })
        .then(() => {
          // on confirm
          cancelOrder({ orderNo: this.orderNo }).then(res => {
        this.init();
      });

        })
        .catch(() => {
          // on cancel
        });
      
    },
    buyAgain(){
      this.orderDetail.prodList.forEach(e => {
        this.$store
          .dispatch("addCart", { skuCode: e.skuCode, num: e.productQuantity })
          .then(res => {});
      });

      this.$toast("Items have been added to Cart .");
    },
    refund(item){
      this.$router.push({path:'exchange',query:{skuCode:item.skuCode,orderNo:this.orderNo}})
    },
    received(){
      confirmOrder({
        orderNo:this.orderNo
      }).then(res=>{
          this.init();
      });
    }
  },
  StringToDate(DateStr)  {   
      var converted = Date.parse(DateStr);  
      var myDate = new Date(converted);  
      if (isNaN(myDate))  
        {   
            //var delimCahar = DateStr.indexOf('/')!=-1?'/':'-';  
            var arys= DateStr.split('-');  
            myDate = new Date(arys[0],--arys[1],arys[2]);  
        }  
        var now = myDate;
        var year = now.getFullYear();       //年
        var month = now.getMonth() + 1;     //月
        var day = now.getDate();            //日
       
        var hh = now.getHours();            //时
        var mm = now.getMinutes();          //分
        var ss = now.getSeconds();          //秒

        if(day < 10)
            clock += "0";
           
        var clock = day + "/";

        if(month < 10)
            clock += "0";
       
        clock += month + "/";

        clock += year + " ";
       
       
        if(hh < 10)
            clock += "0";
           
        clock += hh + ":";

        if (mm < 10)
          clock += '0'; 

        clock += mm + ":";
 
        if (ss < 10)
          clock += '0';

        clock += ss;
        return(clock); 

    },
    getStr(obj){
      var index = obj.indexOf("-");
      if(index != -1){
        var str = obj.substring(0,index-1);
        return str;
      }else{
        return obj;
      }
    }
};
</script>

<style lang="scss" scoped>
.orderDetail {
.van-hairline--top-bottom::after{ border-width: 0 0 1px 0;}
[class*=van-hairline]::after{transform: scale(1); left:15px; right:0; top:auto; bottom:0;border-color:#e5e5e5;}
  li {
    border-top: 10px solid #f6f6f6;
    padding-bottom: 15px;
    .basicInforation {
      .coat {
        width: 100%;
        padding: 10px 4% 0;
        display: -webkit-box;
        span {
          width: 33%;
          display: block;
          color: #666666;
          font-size: 13px;
        }
        .value {
          width: 66%;
          display: flex;
          align-items: flex-end;
          font-size: 14px;
          color: #111111;
        }
      }
    }
  }
}
.van-cell__value {
  color: #e33946;
  font-size: 13px;
}
.van-cell {
  font-size: 15px;
  color: #333333;
}
.commmoWrap {
  padding: 0 4%;
  p {
    font-size: 12px;
    padding: 5px 0;
    color: #666666;
  }
}
.commmo {
  display: -webkit-box;
  width: 100%;
}
.photoWrap {
  width: 89px;
  height: 89px;
  background-color: #f2f2f2;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 4%;
  img {
    width: 69px;
    height: 53px;
  }
}
.information {
  width: 240px;
  .coat {
    width: 100%;
    padding: 2px 0 0 0 !important;
    display: -webkit-box;
    span {
      width: 177px !important;
      display: block;
      color: #000;
      font-size: 13px;
    }
    .value {
      width: 65px !important;
      color: #b7b7b7;
      text-align: right;
      font-size: 12px !important;
      display: block !important;
    }
  }
  .model {
    width: 100%;
    padding: 5px 0 8px 0;
    display: -webkit-box;
    span {
      width: 177px;
      display: block;
      color: #b7b7b7;
      font-size: 13px;
    }
    .value{
        width: 65px;
        color: #e33946;
        text-align: right;
        font-size: 12px;
    }
  }
  .retreat{
      width: 100%;
      padding: 2px 0 0 0;
      display:flex; justify-content: space-between; align-items: center;
      .title{
          width: 177px;
          display: block;
          color: #000;
          font-size: 13px;
          p{
            padding: 0;
          }
          .present{
            font-size: 13px;
            color: #000;
          }
          .origin{
            font-size: 10px;
            text-decoration:line-through;
          }
      }

      .value{
          width: 68px;
          font-size: 13px;
          display: flex; align-items: flex-end;
          span{
            width: 100%;
            height: 25px;
            border: #999999 1px solid;
            text-align: center;
            padding:0;
            color: #333333;
            line-height: 23px;
            border-radius: 3px;
          }
      }
  }
}
.siteWrap{
    width: 100%;
    padding: 12px 4% 0;
    .addressData{
      display: flex; align-items: center;
      .surname{
        font-size: 14px;
        color: #666666;
        padding-right: 15px;
      }
      .mobile{
        font-size: 14px;
        color: #666666;
        padding-right: 5px;
      }
      .pag{
        font-size: 5px;
        border: #E33946 1px solid;
        color: #E33946;
        border-radius: 2px;
        padding: 1px 2px;
      }
    }
  }
  .present {
    font-size: 13px;
    color: #000;
  }
  .origin {
    font-size: 10px;
    text-decoration: line-through;
  }
.value {
  width: 68px;
  font-size: 13px;
  display: flex;
  align-items: flex-end;
  span {
    width: 100%;
    height: 25px;
    border: #999999 1px solid;
    text-align: center;
    padding-top: 2px;
    color: #333333;
    line-height: 24px;
  }
}


.siteWrap {
  width: 100%;
  padding: 12px 4% 0;
  .surname {
    font-size: 14px;
    color: #666666;
    padding-right: 15px;
  }
  .mobile {
    font-size: 14px;
    color: #666666;
    padding-right: 5px;
  }
  .pag {
    font-size: 5px;
    border: #e33946 1px solid;
    color: #e33946;
    border-radius: 2px;
  }
}
.address {
  font-size: 14px;
  width: 100%;
  word-break: break-all;
  padding-top: 5px;
}
.payment {
  .coat {
    width: 100%;
    padding: 10px 4% 0;
    display: -webkit-box;
    span {
      width: 50% !important;
      display: block;
      color: #666666;
      font-size: 12px;
    }
    .value {
      text-align: right;
      color: #111111;
    }
  }
}
.btmWrap {
  width: 100%;
  display: -webkit-box;
  padding: 15px 4% 0;
  .contact {
    width: 50%;
    font-size: 12px;
    display: flex;
    align-items: center;
    font {
      color: #2b87d5;
    }
  }
  .btn {
    width: 50%;
    display: flex;
    justify-content: center;
    span {
      display: block;
      min-width: 68px;
      height: 25px;
      line-height: 23px;
      border: #e33946 1px solid;
      font-size: 14px;
      text-align: center;
      border-radius: 2px;
      background-color: #e33946;
      color: #fff;
    }
    .buyAgain {
      width: 80px;
      background-color: #fff;
      color: #e33946;
      margin-left: 10px;
    }
  }
}
</style>



